.thead {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 5;
}
.row {
  display: grid;
  /* grid-template-columns: 50px 150px 150px 200px 200px repeat(5, 105px) repeat(5,105px) ; */
  grid-template-columns: min-content repeat(15, 100px);
  font-size: 12px;
  font-weight: 400;

  min-height: 30px;

  color: #fff;
  background-color: rgb(96, 96, 96);
}
.col {
  display: block;
  
  padding: 8px;

  height: 100%;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
